img {
  max-width: 100%;
  object-fit: contain;
}
.page {
  height: 100%;
}
.page__body {
  font-family: "Verdana", sans-serif;
  color: #444444;
  font-size: 16px;
  line-height: 18px;
  margin: 0 auto;
  min-width: 320px;
  height: 100%;
}
.page__form {
  height: 100%;
  display: grid;
  padding: 10px 0;
  grid-template-rows: 1fr 1fr 1fr 1fr 4fr;
}
.page__form textarea {
  resize: none;
}
.page__form-screen {
  text-align: center;
  border: 1px solid #000;
}
.page-header {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.page-main {
  flex-grow: 1;
  min-height: 100%;
  width: 100%;
  position: relative;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}
.page-footer {
  margin: 0 auto;
  align-items: center;
}
.upper-block {
  margin-bottom: 5px;
}
@media (max-width: 767px) {
  .upper-block {
    grid-template-columns: 1fr;
  }
}
.upper-block__title {
  text-align: center;
}
.upper-block__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
.upper-block__link:hover {
  color: inherit;
}
.upper-block div {
  height: 100%;
}
.middle-blocks {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
@media (max-width: 767px) {
  .middle-blocks {
    grid-template-columns: 1fr;
  }
}
.middle-blocks .block:nth-child(3) {
  background-color: lightsalmon;
}
.bottom-blocks {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
@media (max-width: 767px) {
  .bottom-blocks {
    grid-template-columns: 1fr;
  }
}
.bottom-blocks .block {
  display: flex;
  align-items: center;
  justify-content: center;
}
.block {
  border: 1px solid #000;
  margin: 5px;
}
.block--dashed {
  border-style: dashed;
  background-color: lightcyan;
}
.block--moved {
  background-color: lightcoral;
}
.block--closed {
  display: none;
}
.block--opened {
  display: block;
}
.page-form {
  flex-grow: 1;
  height: 100%;
  width: 100%;
  position: relative;
}
